/*=============================================================================
# LAYOUT
==============================================================================*/
#main-content {
  max-width: 900px;
  width: 99%;
  margin: 8rem auto;
  position: relative !important;
  display: inline-block !important;
  text-align: left;
  vertical-align: middle;
  z-index: 10; }

.panel {
  position: relative;
  text-align: center;
  padding: 5rem; }
  @media screen and (max-width: 600px) {
    .panel {
      padding: 2rem; } }

@media (max-width: 400px) {
  #main-content {
    position: absolute !important;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 0 2rem;
    margin: 0 auto 8rem; } }

/*=============================================================================
# NAVIGATION
==============================================================================*/
#site-header {
  padding: 3rem;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20; }
  #site-header .socialmedia {
    float: right;
    transition: opacity 1s 0.7s, transform 1s 0.7s; }
    .js #site-header .socialmedia {
      opacity: 0;
      transform: translateY(100%); }
    .js .loaded #site-header .socialmedia {
      opacity: 1;
      transform: translateY(0); }
  @media screen and (max-width: 600px) {
    #site-header .socialmedia {
      display: none; } }

#primary-navigation {
  z-index: 10;
  display: inline-block; }
  #primary-navigation:after {
    content: '';
    display: block;
    clear: both; }
  #primary-navigation ul {
    display: none;
    list-style: none;
    margin: 1rem 0; }
    #primary-navigation ul:hover .menu-item {
      opacity: 0.1; }
      #primary-navigation ul:hover .menu-item:hover {
        opacity: 1; }
  #primary-navigation li {
    float: left;
    clear: left; }
  #primary-navigation .menu-item {
    display: inline-block;
    padding: 1em 1em 1em 0;
    font-size: 0.75rem;
    color: #d5d5d9;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 0.5em;
    font-weight: 700;
